<template>
  <Layout style="background-color: #fff">
    <Sider hide-trigger :style="{background: '#fff','min-width':'250px','max-width':'250px'}" class="layout-sidebar">
      <Menu ref="leftmenu" :style="{position:'relative'}" :active-name="menuactive.active" theme="light" width="auto"
        :open-names="menuactive.open" @on-select="onMenuItemSelect" accordion>
        <Submenu v-for='(menu,i) in menus' :key='i' :name='menu.name'>
          <template slot='title'>
            <Icon :type='menu.icon' v-if='menu.icon'></Icon>
            {{menu.title}}
          </template>
          <MenuItem v-for='(submenu,j) in menu.children' :key='j' :name='submenu.name' style="background-color: #fff">
            <Icon :type='submenu.icon' v-if='submenu.icon'></Icon>
            {{submenu.title}}
          </MenuItem>
        </Submenu>
      </Menu>
    </Sider>
    <Layout style="padding:0 20px 20px;heigt:100%;overflow-y:scroll" class="layout-content">
      <Header style='heigt:30px;line-height:30px;background-color:inherit'>
        <Breadcrumb :style="{margin: '20px 0'}">
          <BreadcrumbItem>首页</BreadcrumbItem>
          <BreadcrumbItem>{{BreadcrumbItems.prvname}}</BreadcrumbItem>
          <BreadcrumbItem>{{BreadcrumbItems.currname}}</BreadcrumbItem>
        </Breadcrumb>
      </Header>
      <Content style='background-color:#fff;padding:10px;'>
            <keep-alive>
                <router-view v-if="$route.meta.keepAlive"/>
            </keep-alive>
            <router-view v-if="!$route.meta.keepAlive"/>   
      </Content>
    </Layout>
  </Layout>
</template>
<script>
export default {
  data() {
    return {
      BreadcrumbItems: this.$sitemap,
      menuactive: this.$menuactive,
      menus: [
        {
          title: "用户管理",
          icon: "person-stalker",
          name: "MemberBlock",
          children: [
            {
              title: "用户列表",
              name: "MemberList"
            },
             {
              title: "代理人列表",
              name: "ProxyList"
            }
          ]
        },
        {
          title: "商品管理",
          name: "ItemBlock",
          icon: "ios-analytics",
          children: [
            {
              title: "发布商品",
              name: "ItemAdd"
            },
            {
              title: "商品列表",
              name: "ItemList"
            },
            {
              title: "商品分类",
              name: "CategoryList"
            }
          ]
        },
        {
          title: "营销管理",
          name: "SalesBlock",
          icon: "heart",
          children: [
            {
              title: "营销商品",
              name: "SalesItemList"
            },
            {
              title: "秒杀商品",
              name: "SeckillingList"
            },
            // {
            //   title: "优惠券",
            //   name: "CouponList"   
            // }
          ]
        },
        {
          title: "活动管理",
          name: "ArticleBlock",
          icon: "ios-bookmarks",
          children: [
            {
              title: "发布活动",
              name: "ArticleAdd"
            },
            {
              title: "活动列表",
              name: "ArticleList"
            },
            // {
            //   title: "内容分类",
            //   name: "ArticletypeList"
            // }
          ]
        },
        {
          title: "轮播管理",
          name: "SwiperBlock",
          icon: "star",
          children: [
            {
              title: "首页轮播列表",
              name: "SwiperList"
            }
          ]
        },
        {
          title: "订单管理",
          name: "OrderBlock",
          icon: "android-print",
          children: [
            {
              title: "订单列表",
              name: "OrderList"
            },
            {
              title:"维修申请",
              name:"RepairList",
            }
          ]
        },
        {
          title: "资金管理",
          name: "AssetsBlock",
          icon: "social-yen",
          children: [
            {
              title: "申请提现",
              name: "ApplyReimburse"
            }
          ]
        },
        // {
        //   title: "店铺管理",
        //   name: "StoreBlock",
        //   icon: "android-print",
        //   children: [
        //     {
        //       title: "店铺申请",
        //       name: "StoreApply"
        //     },
        //     {
        //       title: "店铺列表",
        //       name: "StoreList"
        //     }
        //   ]
        // },
        {
          title: "系统管理",
          name: "SystemBlock",
          icon: "home",
          children: [
            // {
            //   title: "微信授权",
            //   name: "WxaConfig"
            // },
            // {
            //   title: "",
            //   name: "WxaPayConfig"
            // },
            // {
            //   title: "系统配置",
            //   name: "Setting"
            // },
            {
              title: "管理员列表",
              name: "AdminList"
            },
            {
              title: "公司信息",
              name: "Company"
            }
          ]
        }
      ]
    };
  },
  updated() {
    let that = this;
    if (this.menuactive.active != 0 && this.$refs.leftmenu) {
      this.$nextTick(function() {
        that.$refs.leftmenu.updateOpened();
        that.$refs.leftmenu.updateActiveName();
      });
    }
  },
  watch: {
    $sitemap: {
      handler: (newValue, oldValue) => {
        this.BreadcrumbItems = newValue;
      },
      deep: true
    },
    $menuactive: {
      handler: (newValue, oldValue) => {
        let that = this;
        this.menuactive = newValue;
      },
      deep: true
    }
  },
  methods: {
    onMenuItemSelect: function(name) {
      this.$router.push({ name: name });
    }
  }
};
</script>
<style lang="less">
.layout-sidebar {
  overflow-y: scroll;
}
</style>



